import React, {useState, useEffect, createRef } from 'react';
import { Card, CardActions, CardActionArea, CardContent, CardMedia, Button, Typography } from '@material-ui/core';
import classNames from 'classnames';

import useStyles from './styles.js';

const NewsCard = ({article:{description, publishedAt, source, title, url, urlToImage}, i, activeArticle}) => {
    
    const classes = useStyles();
    const [elRefs, setElRefs] = useState([]);
    const scrollToRef = (ref) => window.scroll(0, ref.current.offsetTop - 50);

    useEffect(() => {
        setElRefs((refs) => Array(20).fill().map((ki,j) => refs[j] || createRef()));
    },[]);

    useEffect(() => {
        if(activeArticle === i && elRefs[activeArticle]){
            scrollToRef(elRefs[activeArticle]);
        }
    },[i,activeArticle, elRefs]);

    return (
        <Card ref={elRefs[i]} className={classNames(classes.card, activeArticle === i ? classes.activeCard : null)}>
            <CardActionArea href={url} target="_blank">
                <CardMedia className={classes.media} image={urlToImage || 'https://www.google.com/url?sa=i&url=https%3A%2F%2Fwww.businesszila.com%2Fwhy-is-news-valuable%2F&psig=AOvVaw2u-hd-nU_jCxPtwG3Ii4nz&ust=1638738303458000&source=images&cd=vfe&ved=0CAsQjRxqFwoTCPDaqoiGy_QCFQAAAAAdAAAAABAD' } />
                <div className={classes.details}>
                    <Typography color="textSecondary" variant="body2" component="h2">{(new Date(publishedAt)).toDateString()}</Typography>
                    <Typography color="textSecondary" variant="body2" component="h2">{source.name}</Typography>
                </div>
                <Typography className={classes.title} gutterBottom variant="h5">{title}</Typography>
                <CardContent>
                    <Typography variant="body2" color="textSecondary" component="p">{description}</Typography>
                </CardContent>
            </CardActionArea>
            <CardActions className={classes.cardActions}>
                <Button size="small" color="primary">Learn More</Button>
                <Typography variant="body2" color="textSecondary" >{i+1}</Typography>
            </CardActions>
        </Card>
    )
}

export default NewsCard;
